import Link from "next/link";
import React, { Component } from "react";
import fetch from "isomorphic-unfetch";
import { Provider } from "react-redux";

import store from "../store";
import App from "../App";
const Index = () => {
  <Provider store={store}>
    <App />
  </Provider>;
};
export default Index;



